import { getBanner, getPersonalized, getNewAlbum, getTopAlbum } from '@/api/index.js'
import { timeFormat } from '@/utils/utils.js'
import SongList from '@/components/songList/SongList.vue'
const targetsAlbum = new Map([
	[1, () => getNewAlbum()],
	[2, () => getTopAlbum({ limit: 10 })]
])
export default {
	data() {
		return {
			swiperList: [],
			contentBar: [
				{ name: "每日推荐" },
				{ name: "歌单" },
				{ name: "排行榜" },
				{ name: "电台" },
				{ name: "直播" }
			],
			recommendSongs: [],
			latestAlbum: [],
			newType: 1,
			timeFormat: timeFormat
		}
	},
	components: {
		SongList
	},
	onLoad() {
		this.getBanner()
	},
	methods: {
		getBanner() {
			Promise.all([getBanner({ type: 2 }), getPersonalized({ limit: 6 }), getNewAlbum({ limit: 10 })]).then(res => {
				console.log(res)
				this.swiperList = res[0].banners
				this.recommendSongs = res[1].result
				this.latestAlbum = res[2].albums
			})
		},
		switchTab(type) {
			this.newType = type
			const target = [...targetsAlbum].filter(item => item[0] == type)
			target.forEach(([key, value]) => { value().then(res => { this.latestAlbum = res.albums }) })
		}
	}
}
